.container {
  display: flex;
  justify-content: space-around;

  .radio-group {
    display: flex;
    align-items: center;
    height: 36px;
    margin-right: 40px;

    .radio-btn {
      width: 18px;
      height: 18px;
      cursor: pointer;
    }

    .radio-name {
      font-size: 1.6rem;
      font-weight: 500;
      color: #1e0d03;
      margin-left: 8px;
      cursor: pointer;
    }
  }
}
